export class Company {
  companyId: any;
  companyName: String; 
  isActive: any;
  groups: any = []
  creator : any;
  modifier : any;
}

export class User {
  firstName : String;
  lastName : String;
  emailAddress : String;
  isActive : any = true;
  isCustomerAdmin : any;
  isSuccessHackerAdmin : any;
  groups : any = [];
  company : any = "";
  creator : any;
  modifier : any;
}

export class CompanySearch {
  page: any = 1;
  limit: any = 25;
  user: any;
  isActive: any = "";
  sort: any = "companyName";
  sortOrder: any = "ASC";
}

export class UserSearch {
  page: Number = 1;
  limit: any = 25;
  company: any = "";
  user: any = "";
  isActive: any = "";
  group: any = "";
  search: any = "";
  sort: any = "lastName";
  sortOrder: String = 'ASC';
}

export class ImportStatusSearch {
  company: any = "";
  user: any = "";
  source: any = "";
  status: any = "";
  page: Number = 1;
  limit: any = 25;
  sort: any = "insertTimestamp";
  sortOrder: String = 'ASC';
}


